@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
html, body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: 'Lato' !important;
}

.bg-yellow {
  background: yellow;
}

.bg-green {
  background: green;
}
.main-container {
    height: 100%;
    width: 100%;
    overflow-y: auto;
    min-height: 568px;
}

.main-content {
    max-height: 90vh;
    background: #f4f4f4;
}

.main-box {
    background: #c8e8f5;
    display: grid;
    place-items: center;
    height: 90vh;
}

.box-content {
    background: #fff;
    padding: 15px;
    width: 93vw;
    border-radius: 7px;
}

@media (min-width: 576px) {
.box-content {
    max-width: 500px;
}
}

.title {
    font-size: 1.5rem;
    font-family: 'Righteous';
    font-weight: 400;
    color: #28c5f5;
    background: rgb(255, 255, 255);
    border-radius: 7px;
}

.mnxw-30 {
    min-width: 30px;
    max-width: 30px;
}

.main-todo {
    overflow-x: auto;
    white-space: nowrap;
    padding-left: 0px;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.main-todo::-webkit-scrollbar {
    display: none;
}

.todo-wrapper {
    margin-left: 0px;
    margin-right: 0px;
}

.showTodos {
    max-height: 20vh;
    overflow-y: hidden;
}

.showTodos:hover {
    overflow-y: scroll;
}

@media (max-width: 1024px) {
.showTodos {
    overflow-y: auto;
}

.showTodos:hover {
    overflow-y: auto;
}
}


.checkbox {
    padding-left: 0px;
    padding-top: 2px;
}

.cust-checkbox {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    width: 17px;
    height: 17px;
    border-radius: 4px;
    border: 2px solid #00e5ff;
    margin-top: 1.5px;
    cursor: pointer;
}

input:checked {
    content: url(data:image/png;base64,);
}

.input-todo {
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
    border: 2px solid #00e5ff;
    border-radius: 7px;
    outline: none;
}

button {
    width: 100%;
}

.btn-add {
    background-color: #28c5f5;
    border: none;
    border-radius: 7px;
    color: white;
}

.btn-add:hover {
    opacity: 0.75;
}

.wrapper-clear {
    background: white;
    border-radius: 5px;
}

.btn-clear {
    background: transparent;
    border: none;
    color: rgb(255, 153, 0);
}

.btn-clear:hover {
    opacity: 0.75;
}

.input-todo, .btn-add, .btn-clear {
    height: 45px;
}

.footer {
    max-height: 10vh;
    background: #fff;
}

@media (max-height: 567px) {
.main-content, .main-box {
    min-height: 568px;
}

.footer {
    min-height: 56.8px;
}

.main-container {
    max-height: 568px;
}
}

.footer {
    opacity: 0.4;
    display: grid;
    height: 10vh;
    place-items: center;
    font-size: 0.9rem;
}


a {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}
